        titulo: 'PROGRAMA BIENESTAR DE MADRES SOLTERAS',
        descripcion: 'El Programa fortalece y vincula entre s y con los diversos rganos de gobierno a los Colectivos Comunitarios para que a travs del fomento a sus actividades promuevan el bienestar, la cohesin y capital social, el desarrollo humano y social de grupos, comunidades o regiones que viven en situacin de vulnerabilidad o exclusin, as como el fortalecimiento de expresiones artesanales en el estado de Hidalgo. Desde un enfoque transversal y con perspectiva de gnero, el Programa busca contribuir a promover la igualdad de oportunidades y de trato entre mujeres y hombres. Por lo tanto, contribuye a dirigir los esfuerzos conjuntos para la atencin de grupos de poblacin que viven situaciones de vulnerabilidad o exclusin, propiciando su inclusin en el desarrollo social desde un enfoque de derechos.',
