        requisitos: 'a) Tener residencia en el Estado de Hidalgo o en cualquiera de las 12 regiones del Estado de Hidalgo, de acuerdo con el Plan Estatal de Desarrollo 2022-2028, dando prioridad a Personas en situacin de pobreza o de los 22 municipios de atencin prioritaria indgena del Estado de Hidalgo;\n\nb) Original para cotejo y copia de credencial para votar vigente (INE);\n\nc) Copia legible de CURP actualizada y validada por RENAPO, NICAMENTE en los casos en los cuales el INE de la persona solicitante no lo incluya;\n\nd) Original para cotejo y copia legible de comprobante de domicilio del Estado de Hidalgo (recibo de pago de energa elctrica, telfono, predial, agua o constancia de residencia expedida por la autoridad local), con una vigencia no mayor a tres meses anteriores a la fecha de la solicitud, NICAMENTE en los casos en los cuales el INE de la persona solicitante no lo incluya o no coincida con la indicada en el INE; \n\ne) Breve escrito donde se auto reconozca como parte de la comunidad LGBTTTI; \n\nf) Plan de negocio inclusivo o proyecto de evento de promocin de derechos de la comunidad LGBTTTI o proyecto de fortalecimiento y salud de la comunidad LGBTTTI;\n\ng) Contestar el estudio socioeconmico; y\n\nh) Solicitud de apoyo dirigida al C. Gobernador Constitucional del Estado con atencin a la Persona Titular de la Secretara de Bienestar e Inclusin Social del Estado.',
