        apoyo: 'a) La ganadora o el ganador en traduccin de poesa recibir un diploma y la cantidad de  $150,000.00 (Ciento cincuenta mil pesos, 00/100 M.N.). \n\nb) La ganadora o el ganador de traduccin de narrativa recibir un diploma y la cantidad de $150,000.00 (Ciento cincuenta mil pesos, 00/100 M.N.).',
