        objetivo: 'Impulsar el desarrollo de la creacin literaria entre la poblacin joven a travs del premio de crnica literaria y periodstica, que lleva el nombre del hidalguense Ricardo Garibay cuyo reconocimiento como cronista rebasa las fronteras.',
