        apoyo: 'Se otorgar un Premio nico e indivisible de $100,000.00 (Cien mil pesos 00/100, M.N.) en cheque y diploma. La Secretara de Cultura del Gobierno de Mxico, tendr la primera opcin de publicacin.',
