        titulo: 'PROGRAMA IMPULSANDO A DESCUBRIR',
        descripcion: 'A partir de 2009, se estableci que los beneficiados de este programa visitaran exclusivamente este museo, a fin de disponer del tiempo suficiente para recorrer la totalidad de las reas de exhibicin musestica, el Planetario, el Dinoparque, el Jardn Botnico y el Paseo Arqueolgico.',
        objetivo: 'El Programa Impulsando a Descubrir favorece una educacin inclusiva, equitativa y de calidad, promoviendo oportunidades de aprendizaje mediante el  acercamiento de la poblacin objetivo al conocimiento de la ciencia, el arte y la tecnologa, a travs del juego interactivo y la reflexin, en un ambiente de respeto, equidad, confianza y apertura, al fortalecer los conocimientos adquiridos en el saln de clases, mediante la visita al Museo Interactivo para la Niez y la Juventud Hidalguense  "El Rehilete".',
        poblacion: 'Menores de escuelas primarias pblicas, generales, indgenas y CONAFE de todo el Estado de Hidalgo, preferentemente de 5 y 6 grado, por lo que el rango de edad poblacional flucta entre los 10 aos y 14 aos de edad.',
        modalidad: 'Visita al Museo Interactivo para la Niez y la Juventud Hidalguense El Rehilete.',
        requisitos: 'a) Ser alumno de alguna escuela primaria pblica, general, indgena o de CONAFE.\n\nb). Ser docente o padre de familia o tutor de alguno(s) menores beneficiados.\n\nLo  anterior,  derivado  del  hecho  de  que  para  fines  de  su  visita  al  museo,  los  alumnos  debern  ser acompaados por un grupo mnimo necesario de adultos encabezados por el docente del grupo y algunos padres de familia o tutores para su cuidado. Durante el ejercicio 2024, se beneficiar a visitantes de  escuelas pblicas (alumnos, docentes y padres de familia) de diferentes  municipios del Estado de Hidalgo. Cabe sealar que la programacin puede verse modificada de acuerdo a las condiciones sanitarias y a los lineamientos que marquen las autoridades federales y estatales.',
        apoyo: 'a) Transporte de ida y vuelta de su lugar de origen a la Ciudad de Pachuca de Soto;\n\nb) Hospedaje para pernoctar una noche y alimentacin;\n\nc) Refrigerios; y\n\nd) Distintivos del museo.',
        fecha: 'Sin especificar.',
        contacto: '771 7112044/771 7115596/771 71 15564.',
