        apoyo: 'Monto mximo de $33,000.00 (Treinta y tres mil pesos 00/100 M.N.), por cada persona emprendedora beneficiada.',
        fecha: 'Sin especificar.',
        contacto: '771 676 08 99/771 676 18 44\nBoulevard Felipe ngeles nmero 605, Colonia Venta Prieta, C.P 42083, Pachuca de Soto, Hidalgo.\nfinanciamientoempresarial@hidalgo.\ngob.mx.',
