        requisitos: 'a) Inscribirse en    la Plataforma Digital    de    Becas    para    la    Sostenibilidad    en https://becasparalasostenibilidad.hidalgo.gob.mx por cuenta  propia,proporcionando  la  informacin  y documentacin requerida;\n\nb)Ser egresada o egresado de alguna Institucin de Educacin Superior pblica del estado de Hidalgo, con  los  niveles  de  Tcnico  Superior  Universitario  o  Licenciatura  de  Carreras  Afines  al  Sector Energtico;\n\nc)Tener un promedio general superior o igual a 8.0 en el certificado de estudios;\n\nd)Sermayorde 18 aos.e)Tener nacionalidad mexicana; y\n\nf)Radicar en el estado de Hidalgo.\n\nDocumentos:\na)Ttulo profesional;\n\nb)Cdula profesional;\n\nc)Credencial para votar vigente, de la cual la direccin contenida deber coincidir con el comprobante de domicilio compartido, en caso de no coincidir deber presentar una constancia de radicacin de alguno de los 84 municipios del estado de Hidalgo;\n\nd)ClavenicadeRegistrodePoblacin(CURP) vigente;\n\ne)Acta de nacimiento vigente;\n\nf)Certificado de terminacin de estudios o documento con validez oficial emitido por la institucin educativa que lo acredite;\n\ng)Comprobante de domicilio con ubicacin en el estado de Hidalgo, recibo de agua o luz con una antigedad no mayor a 3 (tres) meses y que coincida con el domicilio de la credencial para votar compartida;\n\nh)Currculum Vitae;\n\ni)Carta compromiso con el Programa debidamente firmada, la cual estar disponible en la plataforma digital https://becasparalasostenibilidad.hidalgo.gob.mx;\n\nj)Carta protesta de decir verdad debidamente firmada, donde garantice no recibir alguna beca de manera simultnea de otro programa social, que sea otorgada por alguna dependencia o entidad de la Administracin Pblica Federal o Estatal, y que estar disponible en la plataforma digital https://becasparalasostenibilidad.hidalgo.gob.mx; y\n\nk)Suscripcin de un Pagar por el monto total de la beca otorgada por el Programa, el cual ser restituido una vez que se d cumplimiento al Programa obteniendo la certificacin correspondiente por parte de la Persona beneficiaria. El formato estar disponible en la plataforma digital. https://becasparalasostenibilidad.hidalgo.gob.mx.',
        apoyo: 'Becas para Sostenibilidad: El monto asignado al programa para el ejercicio presupuestal 2024 ser de hasta $2,000,000.00 (Dos millones de pesos 00/100 M.N.). Dicha asignacin estar sujeta a la disponibilidad presupuestal y financiera.',
