        titulo: 'PROGRAMA FORTALECIMIENTO A LA EDUCACIN BSICA Y MEDIA SUPERIOR, PARA DISMINUIR EL REZAGO EDUCATIVO',
        descripcion: 'El IHEA deber verificar que se cuenta con la disponibilidad presupuestaria para el otorgamiento del apoyo econmico a las personas voluntarias beneficiarias del subsidio activas y personas voluntarias que apoyen en asesoras, aplicacin de exmenes o actividades de preparatoria abierta en el IHEA.',
        objetivo: 'Fortalecer los servicios educativos de alfabetizacin, primaria y secundaria para personas jvenes y adultas de 15 aos o ms y de educacin media superior en su modalidad de preparatoria abierta para disminuir el rezago educativo en el Estado de Hidalgo a travs del otorgamiento de un apoyo econmico a las personas que voluntariamente participen en la prestacin de dichos servicios.',
