        modalidad: 'Apoyo econmico.',
        requisitos: 'Para ser beneficiario del apoyo econmico bastar con acreditar la categora de persona voluntaria beneficiaria del subsidio, (asesor hispanohablante, asesor bilinge u operativo territorial) encontrarse activa y vigente en el padrn de beneficiarios del IHEA conforme a las ROP y haber cubierto satisfactoriamente los criterios sealados en los mecanismos de seleccin de las RO.\n\n Para el caso de las personas voluntarias que apoyan en actividades de Preparatoria Abierta en el IHEA debern reunir los siguientes requisitos: \na) Contar con estudios de Licenciatura o Tcnico Superior Universitario;\n\nb) Edad mnima: 20 aos;\n\nc) Residencia en la ciudad donde se encuentra ubicada la Oficina de preparatoria Abierta a la que voluntariamente pretenda apoyar;\n\nd) Contar con una cuenta bancaria asociada al RFC; y\n\ne) Haber cubierto satisfactoriamente los criterios sealados en los mecanismos de seleccin de las presentes RO.\n\nPara el caso de las personas voluntarias que apoyan en aplicaciones de exmenes de Preparatoria Abierta en el IHEA debern reunir los siguientes requisitos:\na) Contar con escolaridad mnima de Bachillerato;\n\nb) Edad mnima: 20 aos;\n\nc) Residencia  en  la  ciudad  donde  se  encuentra  ubicada  la  Oficina  de  Preparatoria  Abierta  a  la  que voluntariamente pretenda apoyar;\n\nd) Contar con una cuenta bancaria asociada al RFC; y\n\ne) Haber  cubierto  satisfactoriamente  los  criterios  sealados  en  los  mecanismos  de  seleccin  de  las presentes RO.\n\nPor lo que hace a las personas voluntarias que apoyan en asesoras de Preparatoria Abierta en el IHEA debern reunir los siguientes requisitos:\na) Escolaridad mnima: Ttulo de Profesional Asociado al profesiograma para la Preparatoria Abierta emitido por la Direccin de Coordinacin Acadmica, de la Direccin General de Bachillerato, Subsecretara de Educacin Media Superior de la Secretara de Educacin Pblica, Tcnico Superior Universitario o de alguna Licenciatura afn al campo disciplinar al que pertenece la o las asignaturas o mdulos que estarn a su cargo;\n\nb) Para las asignaturas de ingls, los asesores debern tener ttulo o certificado de estudios expedido por alguna institucin perteneciente al Sistema Educativo Nacional;\n\nc) Contar con una cuenta bancaria asociada al RFC;\n\nd) Haber cubierto satisfactoriamente los criterios sealados en los mecanismos de seleccin de las presentes RO; y\n\ne) Cumplir con los requerimientos de la capacitacin en tiempo y forma.\n\nDe los documentos:\na) Las personas voluntarias beneficiarias del subsidio que cumplan los requisitos establecidos en las  RO no requerirn presentar documentacin adicional para ser candidato a la entrega del apoyo econmico;\n\nb) Para el caso de las personas voluntarias que apoyan en asesoras, aplicaciones de exmenes o actividades de preparatoria abierta en el IHEA debern reunir la siguiente documentacin:\nI. Llenar la Solicitud de Ingreso (Anexo 7);\nII. Copia de Acta de Nacimiento;\nIII. Comprobante de estudios;\nIV. Copia simple del INE;\nV.CURP; y\nVI. Dos fotografas tamao infantil.',
