        apoyo: 'Los recursos para la implementacin de este programa corresponden al presupuesto autorizado para el ejercicio fiscal 2024, asignado al programa en el Presupuesto hasta por $100,000.00 (Cien mil pesos 00/100 M.N.), dicha asignacin est sujeta a disponibilidad presupuestal y financiera.',
